.game {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100;
  margin: 0;
}

.game-info {
  width: 20%;
  margin: 2em;
  font-size: 1.4em;
  text-align: center;
}

/* portrait */
@media screen and (orientation:portrait) {
  .game-info {
    width: 100%;
  }
}

.status {
  display: block;
  height: 100%;
}



